import  { useState, useEffect }  from 'react';
import { SettingStore } from '../stores';

export default (list)=> {
  const [ trigger, setTrigger ] = useState(0);
  const [ tm, setTm ]  = useState(0);

  useEffect(()=>{

    for (const o in list) {
      if (list[o].callback) { 
        list[o].callback(SettingStore.getState()[list[o].code])
      } 
    }

    const handleSubscribe = SettingStore.subscribe(() => {
      for (const o in list) {
        if (SettingStore.getState()._watcher === list[o].event) {
          setTrigger(new Date().getTime());
          if (list[o].callback) { 
            list[o].callback(SettingStore.getState()[list[o].code])
          }
        }
      }
    });
    return ()=> {
      handleSubscribe();
    }
  }, []);

  useEffect(()=>{
    setTimeout(()=>{
        setTm(new Date().getTime());
      }, 1000); 
  }, [trigger]);
  return { storeData : SettingStore.getState()  }
}