import  { useState, useEffect }  from 'react';
import { Engine } from '../comm';
import  {  useEthereum } from '../contracts';
import { useUtils } from '../customHooks';
import Web3 from 'web3';

export default (poAddress, projectTId )=> {
  const eng = new Engine();
  const {  getRpc, imutable,  Ximutable, currentWallet, getCurrentWallet } = useEthereum();
  const [ projectinfo, setProjectinfo ] = useState({});
  const [nfts, setNfts ] = useState([]);

  useEffect(()=>{
    getProject();
  }, [ ]);

  const getProject = ()=>{
    imutable( {
      name: 'suezHub',
      address: poAddress,
      method : ['uri'],
      data : {uri:[projectTId]}
    },v0=>{
      if (v0.status === 'success') { 
        const projectMainUri = !v0.data || !v0.data.uri ? '' : v0.data.uri;
        if (projectMainUri) {
          eng.ipfsFetch(projectMainUri , {}, (v=>{
            const projectinfo= {};
            const testNet = !v || !v.app || !v.app.myProject ? '' : v.app.myProject.network;
            const testPAddr = !v || !v.app || !v.app.myProject ? '' : v.app.myProject.address;
            const imageurl = !v || !v.app || !v.image ? '' : v.image;
            projectinfo.address = testPAddr;
            projectinfo.network = testNet;
            projectinfo.image = imageurl;
            projectinfo.netServiceProvider = getRpc(testNet);
            getStatus(projectinfo);
          }))
        } 
      }
    });
  }

  const getStatus = (projectinfo)=>{
    Ximutable(new Web3(projectinfo.netServiceProvider), {
      name: 'suezProject',
      address: projectinfo.address,
      method : ['_tokensCount']
    },v=>{
      if (v.status === 'success') { 
        projectinfo.locked = !v.data || !parseInt(v.data._tokensCount) ? true : false;
      } else {
        projectinfo.locked = true;
      }
      setProjectinfo(projectinfo);
    });
  }

  const getNfts = (tokenId)=>{
    const method =  !tokenId ? ['_getAllTokensUrls'] : ['uri'];
    const params =  !tokenId ? {_getAllTokensUrls:[]} : {uri:[tokenId]};
    Ximutable(new Web3(projectinfo.netServiceProvider), {
      name: 'suezProject',
      address: projectinfo.address,
      method : method,
      data : params
    },v=>{
      if (v.status === 'success') { 
        if ( !tokenId) {
          setNfts(!v.data || !v.data._getAllTokensUrls ? [] : v.data._getAllTokensUrls);
        } else {
          setNfts(!v.data || !v.data.uri ? [] : [ v.data.uri ]);
        }
      }
    });
  }

  return { projectinfo, getProject, getStatus, getNfts, nfts }
}