import React, { useEffect, useState, useContext }  from 'react';
import { Container, Dropdown, Image} from 'react-bootstrap';

import Web3 from 'web3';

import WalletConnectProvider from "@walletconnect/web3-provider";

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTimesCircle } from '@fortawesome/free-solid-svg-icons'

import { UserContext } from '../comm';
import { useUtils, useUIComponents  } from '../customHooks';
import { useEthereum } from '../contracts';

import detectEthereumProvider from '@metamask/detect-provider';
import { SettingStore } from '../stores';

export default (props)=> {
 
  const { networksMeta} = useContext(UserContext);
  const { strTpl } = useUtils();
  const  { showAccount } = useUIComponents();
  const {  getCurrentWallet, valueCurrentWallet, valueCurrentWeb3 }  = useEthereum();
  const [ tm, setTm ] = useState(0);
  const isLocalWallet = !window.ethereum ? false : true;
  // ==== walletProvider===
  const provider = new WalletConnectProvider({
    infuraId: SettingStore.getState().INFURA_API_KEY,
    bridge: "https://bridge.walletconnect.org",
    qrcode: true  
   });
 
  provider.on("disconnect", (code, reason) => {
    console.log('--dis -- connected---');
  });

  const getLocalWallet = ()=> {
    disconnectWallet();
    valueCurrentWallet({provider:'local'});
  
    refreshStore('local');
  }

  const disconnectWallet = async ()=> {
    if (getCurrentWallet().provider === 'walletConnect') {
      window.localStorage.removeItem('walletconnect');
      provider.disconnect();
      valueCurrentWallet({provider:null, account:null, balance:null,network:{}});
      valueCurrentWeb3(null);
   } else  if ( getCurrentWallet().provider === 'local' ) {
      valueCurrentWallet({provider:null, account:null, balance:null,network:{}});
      valueCurrentWeb3(null);
      // disconnectLocal();
   } else {
     console.log('no dupli');
   }
  }
  const refreshStore = async (source)=> {
    if (source === 'walletConnect' || (!!window.ethereum &&  source === 'local')) {
      const web3 = new Web3(source === 'walletConnect' ? provider : Web3.givenProvider);
      valueCurrentWeb3(web3);
      const chainId = await web3.eth.getChainId();
      valueCurrentWeb3(web3);
      web3.eth.getAccounts().then(async v=> {
        if (!v || !v[0]) {
          window.ethereum.request({
            method: 'wallet_requestPermissions',
            params: [{
              eth_accounts: {},
            }]
          })

        } else {
          const idx = networksMeta.findIndex(v=>v.chainId ===  parseInt(chainId));
          const network = networksMeta[idx];
          const balance = await web3.eth.getBalance(v[0]);
          const faucet_tpl = !network || !network.faucets || !network.faucets.length ? '' : network.faucets[0];
          const faucet = !faucet_tpl ? '' : strTpl(faucet_tpl, {ADDRESS:v[0]});
          valueCurrentWallet({provider:source, network:network, account:v[0], balance: balance, faucet: faucet});
          setTm(new Date().getTime())
       //   refreshStore();
        }
        });

    } else {
      console.log('No walletconnect on===>');
    }
  }
 
  provider.on('accountsChanged', async () => {
    refreshStore('walletConnect');
  });

  provider.on('chainChanged', (accounts) => {
    refreshStore('walletConnect');
  });

  const getRemoteWallet = async ()=> {
      disconnectWallet();

      provider.enable().then(v=>{
        valueCurrentWallet({provider:'walletConnect'});
        refreshStore(refreshStore);

      }).catch(async err => {
        window.localStorage.removeItem('walletconnect');
        provider.isConnecting = false;
        provider.disconnect();
         
         try {
           await provider.disconnect();
         } catch (e) {
         }   
      });
  }
  const setLocalEvents = async ()=> {
    if (window.ethereum) {
      const provider =  await detectEthereumProvider();
      if (provider) {
        const elist = ['accountsChanged', 'chainChanged'];
        for (let i = 0 ; i < elist.length; i++) {
          provider.on(elist[i], (account)=>{
            refreshStore('local');
          });
        }
      } else {
      }
    }
  }
   
  useEffect(()=>{
    setLocalEvents();
    const currentWallet =  SettingStore.getState().currentWallet;
    if (currentWallet && currentWallet.provider) {
      refreshStore(currentWallet.provider);
    }
    
  },[]);

  return !getCurrentWallet() ? '' : <span className="float-right text-left p-0 pt-2 pl-3 m-0 ml-3 text-light">
  <Dropdown className="float-right text-left p-0 m-0" style={{minWidth:'26rem'}}>
    <Dropdown.Toggle variant="btn border-info border-circle  alert-warning btn-md text-left">
      <Container fluid={false}  style={{width:'22rem'}} className="float-left text-left p-0 pl-1 pr-1 m-0">
        {'wallet.account' === null ? 'Link to wallet':
          <span>
            <Container className="p-0 m-0 float-left"  style={{maxWidth:'1rem'}} onClick={()=>disconnectWallet()} >
              {!getCurrentWallet().provider ? '' : <FontAwesomeIcon size="1x" icon={ faTimesCircle } onClick={()=>'setWallet( initailAccount)' } className="m-0 p-0"/>}
            </Container>  
            <Container className="p-0 m-0 ml-2 float-left"  style={{maxWidth:'18rem'}}>

              {!!getCurrentWallet().provider ? <span>{getCurrentWallet().network.name}
              {showAccount(getCurrentWallet().account, getCurrentWallet().balance)}</span> : 'Choose your crypto wallet'}

            </Container>
            <Container className="p-0 m-0 float-right"  style={{maxWidth:'1rem'}}>
            {getCurrentWallet().provider === 'local' ? <Image src="/static/media/metamask.png" alt="Icon" style={{width:'1rem'}}/> :
            getCurrentWallet().provider === 'walletConnect' ?  <Image src="/static/media/walletConnectIcon.svg"  className="ml-2" alt="Icon"/>
            : ''}
            </Container> 
          </span>
        }
        <Container className="clearfix"></Container>
        </Container> 
    </Dropdown.Toggle>
    <Dropdown.Menu  style={{width: '100%'}} variant="light" className="border border-circle mt-1 p-3 shadow">

    <Dropdown.Item href="#"  className="p-0 bg-info">
      <Container fluid={true} className="p-2 pt-3 pb-3 bg-info text-light">
        <Container fluid={true}>Network : <i className="text-warning">{'wallet.network'}</i></Container>
        <Container fluid={true}>Account:  <i className="text-warning">
            {"(!wallet || !wallet.account) ? '' : (wallet.account.slice(0,8) + '...' + wallet.account.slice(-8))"}
          </i></Container>
        <Container fluid={true}>NFTs:</Container>
      </Container>
    </Dropdown.Item>
    
    {!isLocalWallet ? 'Please link your wallet to the application' : 
      <Dropdown.Item href="#" className="p-2 pt-3 pb-3" onClick={()=>{ getLocalWallet()}}>
          MetaMask <Image src="/static/media/metamask.png" className="ml-2" alt="Icon" style={{width:'1rem'}}/>
      </Dropdown.Item>}

    <Dropdown.Item href="#" className="p-2 pt-3 pb-3" onClick={()=> {  getRemoteWallet() }} >walletConnect 
        <Image src="/static/media/walletConnectIcon.svg"  className="ml-2" alt="Icon"/>
    </Dropdown.Item>
    <Dropdown.Divider />

    <Dropdown.Item href="#"  onClick={()=>''} className="p-2">
      About
    </Dropdown.Item>
    <Dropdown.Divider />
       
    </Dropdown.Menu>
  </Dropdown>
  </span>
}

