import React, {useEffect, useState} from 'react';
import { Container } from 'react-bootstrap';

import { SettingStore } from '../stores';
import { useEthereum  } from '../contracts';
import { usePostOffice, useUIComponents, useUtils, useStoreWatcher } from '../customHooks';
import  { SuezHub } from './';

export default (props)=> {

  const { copyableShow, web3utils } = useUtils();
  const UNSTaddress = SettingStore.getState().unst;
  const { imutable} = useEthereum();

  const [ unstInfo, setUnstInfo ] = useState({});
  const { getPostOffices } = usePostOffice();
  const {  fundingIcon } = useUIComponents()

  const initLoad = async ()=>{
    imutable({
      name: 'UNST',
      address: UNSTaddress,
      method : ['getHubs', 'getUNSTinfo']
    },v=>{
      if (v.status === 'success') {
        const data = [];
        for (const i in v.data['getHubs']) {
          data.push({address:v.data['getHubs'][i]});
        }
        SettingStore.dispatch({ type: 'loadPostOffices', data : data});
        setUnstInfo( v.data['getUNSTinfo'])
      }
    });
  }

  useEffect(()=>{
    initLoad();
    const handleSubscribe = SettingStore.subscribe(() => {
      if (SettingStore.getState()._watcher === 'addedPo') {
        initLoad();
      }
    });
    return ()=> {
      handleSubscribe();
    }

  }, [])
  const greenBox =(title, comp) => <span className="float-left m-1 p-1 bg-inverse border border-success rounded"
      style={{fontSize:'0.8rem'}}>{title}: {comp}</span>;

  return <Container fluid={true} className="p-3 border border-warning rounded bg-tranparent-box"> 

  <Container fluid={true} className="p-0 pt-1 clearfix text-left text-warning" style={{minHeight:'5rem'}}>
    <span className="float-left m-0 mr-2 p-1">
      <h5 className="text-warning text">{unstInfo.symbol}
        {!unstInfo || !unstInfo.name? '' : <span className="ml-1 p-1" style={{fontSize:'1.1rem'}}>({unstInfo.name})</span>}
      </h5>
    </span>

    <span className="float-left">
        {greenBox('Contract', copyableShow(UNSTaddress,5))}
        {greenBox('Pool', web3utils.fromWei(!unstInfo || !unstInfo.poolSize? '0' : unstInfo.poolSize, 'ether') + '0^18')}
        {greenBox('WEI Rate', unstInfo.rate)}
        {greenBox('Pool', web3utils.fromWei(!unstInfo || !unstInfo.ethBalance? '0' : unstInfo.ethBalance ,'ether') + ' ETH')}
    </span>

    <Container className="clearfix"></Container>

    {fundingIcon()}
    {getPostOffices().map((v,k)=> !v || !v.address ? '' : <SuezHub key={k} type="itemIcon" address={v.address} contract={v}/>)}
    
    <Container className="clearfix"></Container>

  </Container>
  <Container className="clearfix"></Container>

</Container>
}