import React,  { useState, useEffect}  from 'react';
import { Container, Button, Form, Row, Col } from 'react-bootstrap';
import { useEthereum } from '../contracts';

import { SettingStore } from '../stores';
import { Engine } from '../comm';

import { useUtils } from '../customHooks';
import { useParams, useHistory } from 'react-router';

import { ImageLib } from '../comm';

import  { SuezHub } from './';

export default (props)=> {
  const eng = new Engine();
  const history =  useHistory();

  const { poAddress } = useParams();
  const { imutable, mutable, isLocalWalletExist, isWalletMainNet, getMainNet  } = useEthereum();
  const { web3utils } = useUtils();

  const [ formrec, setFormrec ] = useState({name:''});
  const [ formErr, setFormErr ] = useState( '' );

  const [ price, setPrice ] = useState(null);
  const [ tm, setTm ] = useState(0);

  const [ submitted, setSubmitted ] = useState(false);
  
  const [ postOffices, setPostOffices ] = useState([]);

  const { hashLink } = useUtils();

  const handleTextChange = async (e, id)=> {
    const rec = formrec;
    rec[id] = e.target.value;
    setFormrec(rec);
  }

  const [ openRecord, setOpenRecord ] = useState(null);

  const shewMainNetName = ()=> !getMainNet().title? getMainNet().name : getMainNet().title;

  useEffect(()=>{
   // const l = postOffices.filter(v=>v.address === poAddress);
   // setOpenRecord(l[0]);
    const UNSTaddress = SettingStore.getState().unst;
    
    imutable({
      name: 'UNST',
      address: UNSTaddress,
      method : ['askPrice']
    },v=>{
      if (v.status === 'success') { setPrice(v.data['askPrice'])}
    });
  
    setPostOffices(SettingStore.getState().po);
    const handleSubscribe = SettingStore.subscribe(() => {
      if (SettingStore.getState()._watcher === 'loadPostOffices') {
        setPostOffices(SettingStore.getState().po)
      }
    });
    return ()=> {
      handleSubscribe();
    }
  }, []);

  useEffect(()=>{
    if (postOffices.length) {
      const l = postOffices.filter(v=>v.address === poAddress);
      setOpenRecord(l[0]);
      setTm(new Date().getTime());
    }
  }, [postOffices, poAddress]);

  const resetForm = ()=> {
    setSubmitted(false);
    setFormrec({name:''});
    setFormErr( '');
    SettingStore.dispatch({ type: 'addedPo'});
    history.push('/');
  }
  const btnSubmit = ()=> <Button className="btn btn-info m-1" onClick={()=>{
    if (!formrec.name || formrec.name.length < 4 ) {
      setFormErr( 'Name length should not be less than 4 charactors.');
      return true;
    } else {
      setFormErr( '');
    }
    eng.loadingOn();

    /* ---  createPostoffice --- */
    mutable({
        name: 'UNST',
        address: SettingStore.getState().unst,
        method : 'investHub',
        value : web3utils.toWei('1', 'finney'),
        data : [formrec.name]
      },  v=>{
        if (v.status === 'success') {
          setSubmitted(true);
        }
        eng.loadingOff();
      }
    );

  }}>Submit</Button>

const btnCancel = ()=><Button className="btn btn-secondary m-1" onClick={resetForm}>Cancel</Button>

const unAllowedBox = <Container fluid={true} className="p-3 alert-secondary rounded">
      <Row>
        <Col xl={3} lg={3} md={4} sm={5} xs={6} className="p-0 pl-2 pr-2 text-left">
            <Container className="d-flex align-items-center justify-content-center p-2 rounded alert-warning border border-warning" 
              style={{height:'100%', width:'100%'}}>
              <ImageLib v="stop" setting={{className:'', style:{width:'8rem'}}} />
              {/*metaImage.showOrigin('postOffice', {className:'', style:{width:'8rem'}})*/}
            </Container>
        </Col>
        <Col xl={9} lg={9} md={8} sm={7} xs={6} className="p-0 pl-1 pr-3">
          <Container className="alert-warning rounded border border-warning text-left p-3" style={{width:'100%', height:'100%'}}>
            
            Post office is running on <b>{shewMainNetName()}</b> network. Please connect the page to   
            {!isLocalWalletExist ? '' : ' local wallet or '}  walletConnect and switch wallet network to {shewMainNetName()} network
          </Container>
          
        </Col>
    </Row>
</Container>

  const fundingForm  = <Container fluid={true} className="p-3 bg-light rounded">
      <Row>
        <Col xl={3} lg={3} md={4} sm={5} xs={6} className="p-0 pl-2 pr-2 text-left">
            <Container className="d-flex align-items-center justify-content-center p-2 rounded" 
              style={{height:'100%', width:'100%', backgroundColor:'#87CEEB'}}>
              <ImageLib v="postOffice" setting={{className:'', style:{width:'8rem'}}} />
            </Container>
        </Col>
       <Col xl={9} lg={9} md={8} sm={7} xs={6} className="p-0 pl-1 pr-3">
          <Container className="p-3 alert-secondary rounded" style={{width:'100%', height:'100%'}}>
          {!submitted ?  
            <span>
              <Form.Group className="m-0 p-0">
                  <Form.Label className="p-0 m-0 text-info">
                      <Container className="text-dark p-0 m-0 pb-2">
                        Create Post Office under {shewMainNetName()}. Each post office contain 1,000,000 UNST and 1000 
                        placeholder projects. <br/>
                        <b className="text-info">Funding a post offic. Your minimum funding amount is 
                        <span className="p-0 pl-1 pr-1 text-danger">{!price ? '' : web3utils.fromWei(!price ? '' : price, 'ether')}</span> Ether</b>
                      </Container>
                    </Form.Label>
                </Form.Group>

                <Form.Control type="text" defaultValue={formrec.name}
                  placeholder="Enter a office name (Limit 32 characters)" className="p-1 m-0 mb-2 border"
                  onChange={e=>handleTextChange(e, 'name')}/>

                {btnSubmit()} {btnCancel()}
                <span className="m-1 text-danger">{formErr}</span>
            </span> :
            <span>
                Your order has been Submitted! The network {shewMainNetName()} is processing your request.
                <Container className="text-center p-3">
                {hashLink('/postOffices/',<Button className="btn btn-info" onClick={resetForm}>OK</Button>)}
                </Container>      
            </span>}
          </Container>
      </Col>
    </Row>
    </Container>;

const fundingPage =  <Container fluid={true} className="p-0">
  {!isWalletMainNet() ?   unAllowedBox : fundingForm }
</Container>

  return (
    <Container fluid={true} className="p-3">

      <Container fluid={true} className="p-0 bg-light">
        {!poAddress ? '' :  poAddress === 'funding' ? fundingPage : !openRecord ? '' : <SuezHub type="mainCard" contract={openRecord} />  } 
      </Container>
      <Container fluid={true} className="p-0">
      </Container>
    </Container>
  );
}