import React,  { useState, useEffect }  from 'react';
import { Container, Image, Button, Card} from 'react-bootstrap';
import { Engine,  IPFSImage  } from '../comm';

export default (props)=> {
  const eng = new Engine();
  const [ metaData, setMetaData ] = useState({});
  const [ infoboxIcon, setInfoboxIcon ] = useState(true);
  useEffect(()=>{
    eng.ipfsFetch(props.metaDataUri, {}, v=>{
      setMetaData(v);
    });
  },[])

  const nftBox = (v)=> !v.image? '' : <Container className="p-3 m-0 text-center float-left" style={{ width: '25%', minWidth:'20rem'}}>
      <Card border={v.style}  className={`hover-shadow p-0 text-left nft-bg-gradient`} 
        style={{ width:'100%', minWidth:'18rem', height:'13rem' }}>
        <Card.Body className="p-3 rounded" onClick={ ()=> '' }>
            <span className="float-left p-1 pr-3 ">
                <IPFSImage src={!v || !v.image? 'postOffice' : v.image} style={{width:'6rem'}} />

                {/*<Image src={!v || !v.image? 'postOffice' : v.image} style={{width:'6rem'}} />*/}
            </span>
            <Card.Title><b>{v.name}</b></Card.Title>
            <Card.Text className="mb-2">{!v.description? v.name : v.description}
            </Card.Text>
        </Card.Body>
    </Card>
  </Container>

const infoBox = (v)=> <Card.Body className="p-1 rounded round-dark shadow alert-success float-right" 
    style={{ position: 'absolute', right:'1rem', top:'1rem', zIndex:100}} onClick={ ()=> '' }>
      <Container fluid={true} className="p-1 bg-light clearfix"  style={{width:'18rem'}}>
        <IPFSImage src={!v || !v.image? 'postOffice' : v.image} style={{height:'3rem', width:'3rem'}} className="float-left rounded-circle mr-2" />
        <b>{v.name}</b>
        <Card.Text className="m-0 p-0 pl-1"  style={{fontSize:'0.8rem'}}>
        {!v.description ? v.name : v.description }
        </Card.Text>
      </Container>
</Card.Body>

const nftPage = (v)=> !v.image? '' : <Container fluid={true} className="p-0 m-0 text-left">
  <Container fluid={true} className="m-0 p-0 text-left pb-2"><b>{!v.description ? v.name : v.description }</b></Container>
  <Container fluid={true} className="m-0 p-0 text-left" style={{position:'relative'}}>
    {infoBox(v)}
    <iframe  style={{width:'100%', minHeight:'40rem'}}
        src={v.video.url.replace(/mute\=1/, '')}></iframe>
  </Container>
</Container>

  return props.model === 'page' ? nftPage(metaData) :  nftBox(metaData)
}