import React, {  useEffect, useState }  from 'react';
import { Container } from 'react-bootstrap';
import { Banner, Header, PostOffices, StillWorking,  Project, CreateProject, NFTViewer  } from '../modules';
import { useUIComponents  } from '../customHooks';
import { Engine } from '../comm';

import { Switch, Route } from "react-router-dom";

import UItool from './UItool';

export default (props)=> {
  const eng = new Engine();

  const { centrelBox, headerBox, cardListShow, bodyBox } = useUIComponents();
  const [ prodList , setProdList ] = useState([]);

  const loadProducts = ()=>{
    eng.get('/metaData/products.json', {}, v=>{
      if (v.status === 'success') {
        setProdList(v.data);
      }
    });
    
  }
  useEffect(()=>loadProducts(), []);

  return (
    <Container fluid={true}  className="p-0 text-left bg-inverse" style={{minHeight:'58rem'}}>
      {centrelBox(<Banner/>)}
      {headerBox(<Header/>)}
      <Container fluid={true} className="p-0 pt-2 text-center text-secondary home-bg" style={{minHeight:'36rem'}}>
          <Switch>
            <Route exact path={[
                  '/'
                  ]}>
                  {bodyBox(cardListShow(prodList))}
            </Route>

            <Route exact path={[ '/postOffice/:poAddress', '/postOffices' ]}>
                {bodyBox(<PostOffices/>)}
            </Route>

            <Route exact path={[ '/createProject/:poAddress/', '/createProject/']}>
                {bodyBox(<CreateProject/>)}
            </Route>

            <Route exact path={[ '/project/:poAddress/:tokenId', '/createProject/']}>
                {bodyBox(<Project/>)}
            </Route>

            <Route exact path={[ '/nftViewer/:poAddress/:projectId/:nftId']}>
                {bodyBox(<NFTViewer />)}
            </Route>

            <Route exact path={[ '/UItool']}>
                {bodyBox(<UItool />)}
            </Route>

            {/*<Route exact path={[ '/project/:pjAddress/']}>
                {bodyBox(<Project/>)}
                </Route>*/}

            <Route path="/:title">
              {bodyBox(<StillWorking/>)}
          </Route>
          </Switch>
        </Container>
    </Container>
  );
}

