import React,  { useState, useEffect, useContext }  from 'react';
import { Container, Image  } from 'react-bootstrap';
import { UserContext } from '../comm';
import { create } from 'ipfs-http-client';
export default (props)=> {
  const [ base64, setBase64 ] = useState([]);
  const [ tm, setTm ] = useState(0)
  const { imagesMeta } = useContext(UserContext);

  useEffect(()=>{
    (async ()=>{
      console.log(imagesMeta);
      for (const o in imagesMeta) {
        const data = await getBase64FromUrl(imagesMeta[o].ipfs);
        base64.push(data);
      }
      setBase64(base64);
      setTm(new Date().getTime())
    })();
  },[]);

  const getBase64FromUrl = async (ipfs) => {
    const url = `https://ipfs.infura.io/ipfs/${ipfs}`;
    const data = await fetch(url);
    const blob = await data.blob();
    const client = create('https://ipfs.infura.io:5001/api/v0')
    
    return new Promise((resolve) => {
      const reader = new FileReader();
      reader.readAsDataURL(blob); 
      reader.onloadend = async () => {
        const base64data = reader.result;
        const added = await client.add('window._img.'+ipfs+'("' + base64data + '")');
        console.log(ipfs, added.path);
        setTimeout(()=>{
          resolve(base64data);
        }, 30);
      }
    });
  }

return <Container fluid={true} className="p-3" >
  <Container fluid={true} className="p-3 bg-light rounded" style={{minHeight:'32rem'}}>
    URL to JP
    <hr/>
    {base64.map((v, k)=><Image key={k} src={v} className="m-3 border rounded" style={{width:'8rem'}}/>)}
  </Container>
  </Container>
}