import React, {  useEffect, useState }  from 'react';
import './App.css';
import { Container } from 'react-bootstrap';
// import {  HashRouter, BrowserRouter as Router } from "react-router-dom";
import {  HashRouter } from "react-router-dom";

import 'bootstrap/dist/css/bootstrap.min.css';
import { Loading, PopUp, UserContext } from './comm';
import { Body } from './modules';
import LoadMeta from './loadMeta';

function App() {
  const [ sharedData, setSharedData ] = useState({}); 
  const [ isReady, setIsReady ] = useState(false); 
  useEffect(() => {
    const loadMeta = new LoadMeta();
    loadMeta.initLoad(v=>{
      setSharedData(loadMeta.data);
    });
   }, []);

   useEffect(() => {
    if ( Object.keys(sharedData).length) {
      setIsReady(true);
    }
   }, [sharedData]);
    
  return (
    <Container fluid={true}  className="App p-0 text-left">
      {!isReady? '' : <HashRouter>
          <Loading/>
          <PopUp/>
          <UserContext.Provider value={sharedData}>
              {<Body/>}
          </UserContext.Provider>
      </HashRouter>}
    </Container>
  );
}
export default App;
