import React, { useState, useEffect, useRef }  from 'react';
import { Container, Form } from 'react-bootstrap';

import { create } from 'ipfs-http-client';

export default ()=> {
  const client = create('https://ipfs.infura.io:5001/api/v0')
  const [file, setFile ] = useState(null);
  const [fileDataURL, setFileDataURL] = useState(null);
  const [ipfsPath, setIpfsPath] = useState('');

  const inputFileHandle = useRef(null);

  const  uploadVideo = async (e) => {
    
    const files = e.target.files;
    const file = files[0];
    setFile(file);

    return true;
  }

  const addFile = () => {
    setFile(null);
    inputFileHandle.current.click();
  }

  useEffect(() => {
    let fileReader, isCancel = false;
    if (file) {
      fileReader = new FileReader();
      fileReader.onload = (e) => {
        const { result } = e.target;
        if (result && !isCancel) {
          setFileDataURL(result)
        }
      }
      fileReader.readAsDataURL(file);
    }
    return () => {
      isCancel = true;
      if (fileReader && fileReader.readyState === 1) {
        fileReader.abort();
      }
    }

  }, [file]);

  const uploadEntrypoint =(comp)=> <Container fluid={true} className="m-0 p-0"  onClick={addFile} >
        <Form.Control type="file" hidden={true} ref={inputFileHandle} onChange={uploadVideo}  multiple={false}
          accept={'image/*'}/>
      {comp}
    </Container>

  const ipfsImageUpload = async ()=> {
    return new Promise(async resolve => {
      if (file) {
        const added = await client.add(file);
        added.originFileName = file.name;
        setIpfsPath(added.path);
        resolve(added.path);
      } else {
        resolve(false);
      }
    });
  }

  const convertBase64 = async (file) => {
    return new Promise((resolve) => {
        const fileReader = new FileReader();
        fileReader.readAsDataURL(file);
        fileReader.onload = () => {
          resolve(fileReader.result);
        }
        fileReader.onerror = (error) => {
          resolve(false);
        }
    })
  }

  const ipfsJPImageUpload = async ()=> {
    const me = this;
    return new Promise( async (resolve )=> {
      if (file) {
        const added = await client.add(file);
        const x = await convertBase64(file);
        const code = added.path;
        const added1 = await client.add('window._img.'+code+'("' + x + '")');
        resolve({ipfsCode:added.path, jpCode:added1.path});
      } else {
        resolve(false);
      }
    });
  }

  const ipfsUpload = async (data)=> {
      return new Promise(async resolve => {
        const added = await client.add(JSON.stringify(data));
        resolve(added.path);
      });
  }
  const ipfsJPUpload = async (data)=> {
    return new Promise(async resolve => {
      const added = await client.add(JSON.stringify(data));
      const added2 = await client.add(`window.jpQ.Q.${added.path}(${JSON.stringify(data)});`);
      resolve({ipfsCode : added.path, jpCode:added2.path});
    });
  }



  return { 
    ipfsJPUpload, ipfsJPImageUpload,
    uploadEntrypoint, ipfsUpload, ipfsJPUpload, ipfsImageUpload, fileDataURL  }
}