import React  from 'react';
import Web3  from 'web3';
import { Link } from 'react-router-dom';

export default ()=> {
  const web3 =  new Web3();

  const show = (addr, l)=> l?  addr.slice(0,l) + '...' + addr.slice(-1 * l) : addr;

  const showv = (addr, l)=> l?  addr.slice(0,l) + '...' + addr.slice(-1 * l) : addr;

  const copyText = (s)=> !navigator || !navigator.clipboard || !navigator.clipboard.writeText ? 
        '' : navigator.clipboard.writeText(s);

  const copyableShow = (addr,l)=> <span className="copyable" onClick={()=>copyText(addr)}>{show(addr, l)}</span>;

  const hashLink = (url, comp)=> <Link to={url} style={{ textDecoration: 'none' }}>{comp}</Link>;

  const showString = (str, limit)=> (str.length - limit > 3) ? (str.slice(0,limit) + '...') : str;

  const showAddress = (addr, l)=> !addr? '' : l?  addr.slice(0,l) + '...' + addr.slice(-1 * l) : addr;

  const strTpl = function(str, params) {
    const arr = !str ? [] : str.match(/\$\{([^\}]+)\}/g);
    const a = {};
    for (let o in arr) {
      const c = arr[o].replace(/(\$\{|\})/g, '');
      a[c] = '';
    }
    const p = {...a, ...params};
    const names = Object.keys(p);
    const vals = Object.values(p);
    return new Function(...names, `return \`${str}\`;`)(...vals);
  }
  return { show, showv, copyableShow, web3utils:web3.utils, hashLink, showString, strTpl, showAddress }
} 