 //     '0x41798557fB98118f7430e2F443a073D5801005B1'; //--dev ---

const _defaultSetting = {ready: false, loading: {}, popup:false, screenModel:{}, _watcher:'',
      unst : _unstAddr,
      INFURA_API_KEY : '3af2ebd23289490dbe29d3034e2887ce',
      ipfs : {
        main : 'https://ipfs.infura.io/ipfs/',
        Q: ['gateway.ipfs.io', 'dweb.link', 'ipfs.io', 'cf-ipfs.com']
 //       Q: ['gateway.ipfs.io','cloudflare-ipfs.com', 'dweb.link', 'ipfs.io', 'cf-ipfs.com']
/*
        ['gateway.ipfs.io','cloudflare-ipfs.com', 'dweb.link', 'ipfs.io', 'cf-ipfs.com']
        http://gateway.ipfs.io/ipfs/QmQm2B9FR5hCbTnnrnMvr3Gm4SmHNZhbSeq8uAUg4DLvGt
        http://cloudflare-ipfs.com/ipfs/QmQm2B9FR5hCbTnnrnMvr3Gm4SmHNZhbSeq8uAUg4DLvGt
        http://dweb.link/ipfs/QmQm2B9FR5hCbTnnrnMvr3Gm4SmHNZhbSeq8uAUg4DLvGt
        http://ipfs.io/ipfs/QmQm2B9FR5hCbTnnrnMvr3Gm4SmHNZhbSeq8uAUg4DLvGt
        http://cf-ipfs.com/ipfs/QmQm2B9FR5hCbTnnrnMvr3Gm4SmHNZhbSeq8uAUg4DLvGt

        http://cf-ipfs.com/ipfs/QmcwdSu23noteAeNTpU9tWWBhqmY9akzGkVRmquk9qQsds
*/
      },
      mainChainId : 4,
 //     poNet : 'Rinkeby',
      po : [],
      currentWallet : {
        provider : null,
        account : null,
        balance : null,
        network : {}
      },
      currentWeb3 : null,
      contracts : {
        local : [],
        public : []
      }, networks : [],
      walletProvider: null,
      current: { contract : '', account : { account: '', network:''} }
  };


const reducerSetting = (state = _defaultSetting, action) => {
  state._watcher = '';
  state.contracts.local = !state.contracts.local ? [] : state.contracts.local;
  switch(action.type) {

    case 'addLoading':
      state._watcher = 'loading';
      state.loading[action.id] = new Date().getTime();
      return state;
    case 'removeLoading':
      state._watcher = 'loading';
      delete state.loading[action.id];
      return state;

    case 'popupTrigger':
      state._watcher = 'popupTrigger';
      state.popup = action.popup;
      return state;

    case 'alertTrigger':
      state._watcher = 'alertTrigger';
      state.alertMessage = action.alertMessage;
      return state;
// --- ethrume related --- //
  
  case 'valueCurrentWallet':
    state._watcher = 'valueCurrentWallet';
    state.currentWallet = action.value;
    localStorage.setItem('CurrentWallet',  JSON.stringify(state.currentWallet));
    return state;
 
  case 'valueCurrentWeb3':
    state._watcher = 'valueCurrentWeb3';
    state.currentWeb3 = action.value;
    return state;

// --- app related --- //
    case 'addedPo':
      state._watcher = 'addedPo';
      //const poidx = state.po.findIndex(v=>v.addresss === action.data.address);
      // state.po[poidx === -1 ? state.po.length : poidx] = action.data;
      return state;

    case 'updatePo':
      // state._watcher = 'loadPostOffices';
      state._watcher = 'loadPostOffices';
      const poidx = state.po.findIndex(v=>v.address === action.data.address);
      state.po[poidx === -1 ? state.po.length : poidx] = action.data;
      return state;
  
    case 'addMyProject':
      state._watcher = 'addedProject';
      state.myProject = action.project;
      localStorage.setItem('localMyContracts',  JSON.stringify(action.project));
      return state;

    case 'loadPostOffices':
      state._watcher = 'loadPostOffices';
      state.po = action.data;
      return state;

    default:
      return state;
  }
};
const SettingStore = createStore(reducerSetting, composeWithDevTools());

export default SettingStore;
