import React,  { useState, useEffect }  from 'react';
import { Container, Button, Form, Row, Col, Dropdown, Card, Collapse  } from 'react-bootstrap';

import { SettingStore } from '../stores';
import { Engine } from '../comm';

import  { useEthereum } from '../contracts';

import { useUtils,  useIPFSUpload, useStoreWatcher, useUIComponents  } from '../customHooks';

import { useParams, useHistory } from 'react-router';

import { ImageLib } from '../comm';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCloudUploadAlt } from '@fortawesome/free-solid-svg-icons';


export default (props)=> {
  const eng = new Engine();
  const history =  useHistory();

  const { uploadEntrypoint, ipfsImageUpload, ipfsJPImageUpload, fileDataURL, ipfsJPUpload, } = useIPFSUpload();
  const {  isLocalWalletExist, isWalletMainNet, createContract, imutable, mutable, getMainNet, getCurrentWallet } = useEthereum();
  const{ showNetworkName, confirmPopup } = useUIComponents();

  const [ formrec, setFormrec ] = useState({name:'', description : ''});
  const [ formErr, setFormErr ] = useState( '' );

  const [ myProject, setMyProject ] = useState({});
  const [ submitted, setSubmitted ] = useState(false);

  const { poAddress } = useParams();
  const [ poRec, setPoRec  ] =  useState({});

  const { hashLink, show, copyableShow } = useUtils();
  const [ postOffices, setPostOffices ] = useState([]);
  // const [ tm, setTm ] = useState(0);
  // const { projectAsNFT  } = useProject();

  const handleTextChange = async (e, id)=> {
    const rec = { ...formrec};
    rec[id] = e.target.value;
    setFormrec(rec);
    
  }
  const pickDropdown = (v, id)=> {
    const rec = { ...formrec};
    rec[id] = v;
    setFormrec(rec);
    
  }

  const { storeData } = useStoreWatcher([
    {code: 'po', event : 'loadPostOffices', callback: setPostOffices },
    {code: 'myProject', event : 'addedProject', callback:setMyProject }
  ]);

  useEffect(()=>{
  },[]);

  useEffect(()=>{
    if (poAddress && postOffices) {
      const postOffice = (!postOffices ? [] : postOffices).filter(v=>v.address === poAddress);
      setPoRec(!postOffice[0] ? false : postOffice[0]);
    }
  },[ postOffices]);

  const resetForm = ()=> {
    setSubmitted(false);
    setFormrec({name:''});
    setFormErr( '');
    cancelProcess();
  //  SettingStore.dispatch({ type: 'addedPo'});
  //  history.push('/postOffices');
  }
  const cancelProcess = ()=>{
    history.push(!poAddress? `/` : `/postOffice/${poAddress}`);
  }

  const btnCancel = ()=><Button className="btn btn-secondary m-1" onClick={cancelProcess}>Cancel</Button>

  const abandonProject = ()=> {
    eng.popupOn(confirmPopup({
      caption:'It would be unrecoveryable, please confirm to abandon the project.',
      confirm : ()=>{
        eng.popupOff();
        SettingStore.dispatch({type:'addMyProject', project:null});
        cancelProcess();
      }, cancel : ()=>eng.popupOff()
    }));
  } 


  const btnInit = ()=> <Button className="btn btn-info m-1" onClick={()=>{
    eng.loadingOn();
    createContract({
      name: 'suezProject'
    },  v=>{
      if (v.status === 'success') {
        SettingStore.dispatch({type:'addMyProject', project:v.data});
      } 
      eng.loadingOff();
    });
  }}>Create a project</Button>

  const btnSubmit = ()=> <Button className="btn btn-info m-1" onClick={async ()=>{
    const metaData = {};
    eng.loadingOn();
    console.log('--AA1-->', formrec);
    const p = await ipfsJPImageUpload();
    console.log('--AA0-->', p);
   // const p = await ipfsImageUpload();
    console.log('--AA2-->', p);
    if (p === false) {
      setFormErr( 'Missing upload image or a wrong image format.');
      eng.loadingOff();
      return true;
    }
    if (!formrec.name || formrec.name.length < 4) {
      setFormErr( 'Name length should not be less than 4 charactors.');
      eng.loadingOff();
      return true;
    } 
    if (!poRec.address && (!formrec.postOffice || !formrec.postOffice.address) ) {
      setFormErr( 'Need select a post offie to park this project.');
      eng.loadingOff();
      return true;
    }
 
    metaData.name = formrec.name;
    metaData.description = formrec.description;
    metaData.image = `https://ipfs.infura.io/ipfs/${p.ipfsCode}?${p.jpCode}`;
    metaData.attributes = [];
    metaData.app = { myProject :  myProject};

    const p1 = await ipfsJPUpload(metaData);
    
    if (p === false) {
      setFormErr( 'Unable to do ipfs upload!');
      eng.loadingOff();
      return true;
    }
    if (formrec.postOffice || poRec.address) {
      mutable({
        name: 'suezHub',
        address:  !poRec.address ? formrec.postOffice.address : poRec.address,
        method : '_mintToken',
        data : [`https://ipfs.infura.io/ipfs/${p1.ipfsCode}?${p1.jpCode}`]
      },  v=>{
        setFormErr('');
        SettingStore.dispatch({type:'addMyProject', project:null});
        console.log('AAA--1--')
        setSubmitted(true);
        console.log('AAA--2--')
        // resetForm();
        console.log('AAA--3--', `https://ipfs.infura.io/ipfs/${p1.ipfsCode}?${p1.jpCode}`)
        eng.loadingOff();
      });
    }
  }}>Submit</Button>

  const shewNetworkName = (network)=> !network ? '' : !network.title? network.name : network.title;

  
  const projectInit  =  <Container fluid={true} className="p-3 bg-light rounded">
  <Container fluid={true} className="p-3 ">
    <Row>
      <Col xl={3} lg={3} md={4} sm={5} xs={6} className="p-0 pl-2 pr-2 text-left">
          <Container className="d-flex align-items-center justify-content-center p-2 rounded" 
            style={{height:'100%', width:'100%', backgroundColor:'#87CEEB'}}>
            <ImageLib v="project" setting={{className:'', style:{width:'8rem'}}} />
            {/*metaImage.showOrigin('postOffice', {className:'', style:{width:'8rem'}})*/}
          </Container>
      </Col>
      <Col xl={9} lg={9} md={8} sm={7} xs={6} className="alert-secondary rounded p-3">
        <Form.Group className="m-0 p-0">
          <Form.Label className="p-0 m-0 text-info">
              <Container className="text-dark p-0 m-0 pb-2">
                At the first step, to create a project under sidenet. Each project contain 1000 
                placeholder NFT.
                
                {!getCurrentWallet() || !getCurrentWallet().faucet? '' : <Container fluid={true} className="text-left p-0">If you do not have 
                <b>{!getCurrentWallet() || !getCurrentWallet().network || !getCurrentWallet().network.name ? ' test ' : 
                  ` ${getCurrentWallet().network.name} `}</b> ETH, you may apply from 
                    the <a href={getCurrentWallet().faucet} target="_blank">Link of Faucet</a>
                </Container>}
                
                </Container>
            </Form.Label>
        </Form.Group>
        {btnInit()}
        {btnCancel()}
      </Col>
  </Row>
  </Container>
</Container>;

  const projectForm  =  <Container fluid={true} className="p-3 bg-light rounded">
    <Container fluid={true} className="p-3 ">
      <Row>
        <Col xl={3} lg={3} md={4} sm={5} xs={6} className="p-0 pl-2 pr-2 text-left">
              <Container className="d-flex align-items-center justify-content-center p-2 rounded alert-success  clearfix" 
                style={{height:'100%', width:'100%', position:'relative'}}>

                {!fileDataURL?<ImageLib v="project" setting={{className:'', style:{position:'absolute',margin:'40%', width:'8rem',  zIndex:300}}} /> : 
                   <img src={fileDataURL} alt="preview" style={{position:'absolute',margin:'40%', width:'8rem',  zIndex:400}} /> }

                {uploadEntrypoint(<FontAwesomeIcon size="3x" className="text-success cursor-pointer" 
                  style={{position:'absolute', bottom:'0.1rem',right:'0.1rem', zIndex:600}}
                  icon={ faCloudUploadAlt }/>)}

              </Container>
        </Col>

         <Col xl={9} lg={9} md={8} sm={7} xs={6} className="alert-success rounded p-3">
          <Form.Group className="m-0 p-0">
            <Form.Label className="p-0 m-0 text-dark p-0">
                Project:<b className="ml-2">{!myProject || !myProject.address ? '' : copyableShow(myProject.address, 5)}</b>
                <span className="ml-2">on the network  <b>{showNetworkName(!myProject ? '' : myProject.network)}</b></span>

                
              </Form.Label>
          </Form.Group>

          <Form.Group className="m-0 p-0">
            <Form.Label className="p-0 m-0 text-dark pt-2">
              <Dropdown className="p-0 float-left">Join a Post Office
                  <Dropdown.Toggle variant="success" id="dropdown-basic" className="ml-2">
                  {poRec.address ? `[${show(poRec.address,5)}] ${!poRec.data ? '' : poRec.data.name}` : 
                    !formrec.postOffice ? 'Select post office' :  `[${show(formrec.postOffice.address,5)}] ${formrec.postOffice.data.name}`}
                  </Dropdown.Toggle>
                  {poRec.address ? '' :
                    <Dropdown.Menu>
                        {postOffices.filter(v=>v.address !== poAddress).map((v,k)=> <Dropdown.Item key={k} onClick={()=>pickDropdown(v, 'postOffice')}>
                              {!v || !v.address ? '' : `[${show(v.address,5)}] ${!v.data ? '' : v.data.name}`}</Dropdown.Item> )}
                    </Dropdown.Menu>
                  }
              </Dropdown>

              <Button className="btn btn-danger ml-3 float-left" onClick={abandonProject}>Abort the Project</Button>

              <Container className="clearfix"></Container>

            </Form.Label>
          </Form.Group>
          <Container className="p-0 pb-1">

          </Container>


          <Form.Control type="text" defaultValue={formrec.name}
            placeholder="Enter a project name (Limit 32 characters)" className="p-1 m-0 mb-2 border"
            onChange={e=>handleTextChange(e, 'name')}/>

           <Form.Control as="textarea" rows={3} placeholder="Description (Limit 320 characters)" 
            className="p-1 m-0 mb-2 border"  onChange={e=>handleTextChange(e, 'description')} />

            {!isWalletMainNet() ? 
                <Container className="text-info p-0 m-0 pb-2">
                  Remind: You need switch your walllet to Ethereum mainnet to submit. This step will cost real Ethereum. 
                </Container> : btnSubmit()}
              
              {btnCancel()}
          <span className="m-2 text-danger">{formErr}</span>
        </Col>
    </Row>
    </Container>
  </Container>;

const successBox =  <Container fluid={true} className="p-3 alert-secondary rounded">
            Your project has been Submitted!
            <Container className="text-center p-3">
             <Button className="btn btn-info" onClick={ resetForm }>OK</Button>
            </Container>
      </Container>

const unAllowedBox = <Container fluid={true} className="p-3 alert-secondary rounded">
      <Row>
        <Col xl={3} lg={3} md={4} sm={5} xs={6} className="p-0 pl-2 pr-2 text-left">
            <Container className="d-flex align-items-center justify-content-center p-2 rounded alert-warning border border-warning" 
              style={{height:'100%', width:'100%'}}>
              <ImageLib v="stop" setting={{className:'', style:{width:'8rem'}}} />
              {/*metaImage.showOrigin('postOffice', {className:'', style:{width:'8rem'}})*/}
            </Container>
        </Col>
        <Col xl={9} lg={9} md={8} sm={7} xs={6} className="p-0 pl-1 pr-3">
          <Container className="alert-warning rounded border border-warning text-left p-3" style={{width:'100%', height:'100%'}}>
          Project need run on a <b>Test</b> network. Please connect the page to   
            {!isLocalWalletExist ? '' : ' local wallet or '}  walletConnect and switch wallet network to non {shewNetworkName(getMainNet())} network
          </Container>
          
        </Col>
    </Row>
</Container>
const [open, setOpen] = useState(false);
return <Container fluid={true} className="p-3" style={{minHeight:'32rem'}}>
 <Container fluid={true} className="p-0">
  {/*==<span className="bg-light">-{JSON.stringify(postOffices)}==</span>==*/}
     {!isWalletMainNet() ?    unAllowedBox : 
     submitted ? successBox : !myProject || !myProject.address ? projectInit :  
      <Container fluid={true} className="p-0">
        {projectForm }
        <Container fluid={true} className="p-0 mt-3">
        {/*getPostOffices().filter(v=>v.address !== poAddress ).
            map((v,k)=> <span key={k}>
     {hashLink('/postOffice/' + v.address, <SuezHub address={v.address}/>)}
          </span>)*/}
        </Container>
       </Container>}
</Container>
  </Container>
}