import React,  { useState, useEffect }  from 'react';
import { Container } from 'react-bootstrap';
import { Engine } from '../comm';

import  {  useEthereum, useBridge } from '../contracts';
import { useParams, useHistory } from 'react-router'

import { useUtils } from '../customHooks';
import { NftComp, FormMint } from '.';
import Web3 from 'web3';

export default (props)=> {
  const eng = new Engine();
  const { hashLink } = useUtils();
  const { poAddress, tokenId } = useParams('');

  const  { projectinfo, nfts, getNfts, getProject } = useBridge(poAddress, tokenId);

  useEffect(()=>{

  }, []);

  useEffect(()=>{
    getNfts();
    }, [ projectinfo ]);
  
return <Container fluid={true} className="p-3">
    {<Container fluid={true} className="p-3 bg-light rounded" style={{minHeight:'32rem'}}>
      <Container fluid={true} className="clearfix p-3">
        <FormMint project={projectinfo} fn={{ getNfts : getNfts, getProject: getProject }}/>
        <Container className="clearfix"></Container>
      </Container>
      <Container className="clearfix"></Container>

      <Container fluid={true} className="clearfix text-left">
        {nfts.map((v, k)=><span key={k} >
          {!k? '' : hashLink(`/nftViewer/${poAddress}/${tokenId}/${k}`, <NftComp metaDataUri={v}/>)}</span>)}
      </Container>
      <Container className="clearfix"></Container>
</Container>}
  </Container>
}