import React,  { useEffect }  from 'react';
import { Container } from 'react-bootstrap';

import  { useBridge } from '../contracts';
import { useParams } from 'react-router'
import { NftComp } from '.';
import { useUtils } from '../customHooks';

export default (props)=> {
  const { poAddress, projectId, nftId } = useParams();
  const  { projectinfo, getNfts, nfts } = useBridge(poAddress, projectId);
  const { showAddress } = useUtils()
  useEffect((v)=>{
    getNfts(nftId) 
  }, [ projectinfo ]);

  useEffect((v)=>{
    console.log('nfts===>', nfts[0], nfts)
  }, [ nfts ]);

return <Container fluid={true} className="p-3">
    <Container fluid={true} className="p-3 bg-light rounded text-left">
      <h3>NFT viewer
        <span className="ml-3" style={{fontSize:'0.8rem'}}>
          <span className="text-secondary mr-3"><span className="text-info">Post Office:</span>{showAddress(poAddress, 5)} </span>
          <span className="text-secondary mr-3"><span className="text-info">Project:</span>{showAddress(projectinfo.address, 5)}  </span>
        </span>
      </h3>
      {!nfts[0] ? '' : <NftComp metaDataUri={nfts[0]} model="page"/>}
    </Container>
  </Container>
}